@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-duration: 1s;
}

@keyframes fadeOutOpacity {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.fade-out {
  opacity: 0;
  animation-name: fadeOutOpacity;
  animation-iteration-count: 1;
  animation-duration: 1s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  min-height: 100vh;
}

body {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-repeat: no-repeat;
  background-origin: content-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

/* 
    Anchors -- For Sticky Headers
    Placed above section
    https://pixelflips.com/blog/anchor-links-with-a-fixed-header
*/
a {
  text-decoration: none;
  color: #ffffff;
}

h1, h2 {
  color: #fff;
  font-family: 'Roboto Condensed', Helvetica, Arial, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

h1 a, h2 a {
  color: #fff;
}

h1 a:hover, h1 a:focus, h1 a:active, h2 a:hover, h2 a:focus, h2 a:active {
  color: #fff;
  text-decoration: none;
  border-bottom: 1px dashed magenta;
}

a.top {
  position: absolute;
  top: 0;
  left: 0;
}

a.back-to-top {
  color: #fff;
  display: block;
  position: fixed;
  bottom: 12px;
  right: 18px;
  font-size: 33px;
  opacity: 0;
}

a.back-to-top i {
  color: #777;
  display: block;
}

a.back-to-top:hover, a.back-to-top:focus, a.back-to-top:active {
  text-decoration: none;
}

a.back-to-top:hover i, a.back-to-top:focus i, a.back-to-top:active i {
  color: #fff;
  text-shadow: 1px 1px 15px magenta, 1px 1px 15px #ccc;
}

.wrapper {
