#nav-works.nav-works-top a {
  opacity: 0;
  animation-name: fadeOutOpacity;
  animation-iteration-count: 1;
  animation-duration: 1s;
}

#nav-works.nav-works-scroll a {
  opacity: 1;
  animation-name: fadeInOpacity;
  animation-iteration-count: 1;
  animation-duration: 1s;
}

#nav-works a {
  background-color: transparent;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  color: #fff;
  display: block;
  padding: 6px 6px 6px 12px;
  transition: background-color 0.5s ease;
}

#nav-works a i {
  font-size: 24px;
  margin-right: 12px;
}

#nav-works a .label {
  display: inline-block;
  overflow: hidden;
  max-width: 0;
  margin-right: 0;
  white-space: nowrap;
  vertical-align: middle;
  border-bottom: 1px solid #fff;
  text-transform: uppercase;
  font-size: .88em;
  transition: max-width 0.5s;
}

#nav-works a .separator {
  display: inline-block;
  width: 0;
  transition: width 0.1s;
  transition-delay: 0.5s;
}

#nav-works a:focus {
  opacity: 1;
}

#nav-works a:hover, #nav-works a:focus {
  background-color: rgba(0, 0, 0, 0.8);
  background: linear-gradient(45deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.1) 100%);
  text-decoration: none;
  text-shadow: 1px 1px 15px magenta, 1px 1px 15px #ccc;
}

#nav-works a:hover .label, #nav-works a:focus .label {
  max-width: 333px;
  padding-right: 33px;
  transition-delay: 0.1s;
}

#nav-works a:hover .separator, #nav-works a:focus .separator {
  width: 0.3em;
